<template>
  <div class="detail-kelas-saya modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Detail Kelas</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />
    <div class="content-dks">
      <div class="data-detail-user">
        <label for>
          Name
          <span>:</span>
        </label>
        <span>{{ dataProps.name == null ? '-' : dataProps.name }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Mentor
          <span>:</span>
        </label>
        <span>{{ dataProps.mentor == null ? '-' : dataProps.mentor }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Jenis Kelas
          <span>:</span>
        </label>
        <span>{{ dataProps.jenis == null ? '-' : dataProps.jenis }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Jadwal
          <span>:</span>
        </label>
        <span v-if="dataProps.schedule == '-'">-</span>
        <span v-else class="jadwal-dks">
          <p>
            Nama :
            <span>{{ dataProps.schedule.name }}</span>
          </p>
          <p>
            Mulai :
            <span>{{ dataProps.schedule.startDate }}</span>
          </p>
          <p>
            Selesai :
            <span>{{ dataProps.schedule.endDate }}</span>
          </p>
          <p>
            Link Kelas :
            <span>{{ dataProps.schedule.linkKelas == null ? '-' : dataProps.schedule.linkKelas }}</span>
          </p>
        </span>
      </div>
      <div class="data-detail-user">
        <label for>
          Kurikulum
          <span>:</span>
        </label>
        <span>
          <ol class="list-kl">
            <li v-for="(k, idx) in dataProps.curriculum" v-bind:key="idx">
              <p>
                Topik :
                <span>{{ k.topics }}</span>
              </p>
              <p>
                Materi :
                <span>{{ k.materi }}</span>
              </p>
              <p>
                Files :
                <span>
                  <a href="#" @click="downloadFiles( j.files )">{{ j.files }}</a>
                </span>
              </p>
            </li>
          </ol>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailKelasSaya",
  components: {},
  props: ["data"],
  data() {
    return {
      dataProps: {}
    };
  },
  methods: {
    init() {
      let data = this.$props.data;
      this.dataProps = data;
      this.handleData();
    },
    handleData() {
      console.log("data detail kelas ", this.dataProps);
    },
    closeModal() {
      this.$modal.hide("detail-kelas");
    },
    downloadFiles(nameFile) {
      let AuthSth = this.$root.authorization();
      let url = `${this.$root.filesDownload()}/${nameFile}?token=${AuthSth.split(' ')[1]}`;
      window.open(url);
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.detail-kelas-saya {
  padding: 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-dks {
    .data-detail-user {
      font-size: 1em;
      text-align: left;
      display: flex;
      > label {
        font-weight: 600;
        min-width: 100px;
        max-width: 150px;
        > span {
          float: right;
        }
      }
      > span {
        display: inline-block;
        margin: 0 0 0 15px;
        > input,
        > textarea {
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid #797878;
          padding: 3px 7px;
          width: 150%;
        }
      }
    }
    .list-kl {
      padding: 0%;
      margin: 0%;
      > li {
        list-style: none;
        margin: 3px;
        border-bottom: 1px solid #575555;
        padding: 5px;
        > p {
          margin: 0%;
          > span {
            font-weight: 500;
          }
        }
      }
    }
    .jadwal-dks {
      margin: 3px;
      border-top: 1px solid #575555;
      border-bottom: 1px solid #575555;
      padding: 5px;
      > p {
        margin: 0%;
        > span {
          font-weight: 500;
        }
      }
    }
  }
}
</style>