<template>
  <div class="executor-join-mentor modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Daftar Jadi Mentor</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />
    <div class="content-ejm">
      <div class="form-input-mbx">
        <label for="about">About</label>
        <input
          type="text"
          class="form-control"
          name="about"
          placeholder="About"
          v-model="dataExecutor.about"
        />
        <span v-if="!cannotNull.about" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="jobTitle">Job Title</label>
        <input
          type="text"
          class="form-control"
          name="jobTitle"
          placeholder="Job Title"
          v-model="dataExecutor.jobTitle"
        />
        <span v-if="!cannotNull.jobTitle" style="color: red;">This field is required.</span>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelJoin()" class="btn btn-danger">Cancel</button>
      <button @click="submitJoin()" class="btn btn-success">Daftar</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ExecutorJoinMentor",
  components: {},
  data() {
    return {
      dataExecutor: {
        about: "",
        jobTitle: ""
      },
      cannotNull: {
        about: true,
        jobTitle: true
      }
    };
  },
  methods: {
    init() {},
    closeModal() {
      this.$modal.hide("executor-join-mentor");
    },
    submitJoin() {
      let body = this.dataExecutor;
      this.postJoin(body);
    },
    postJoin(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.joinMentorUser(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostJoin().success(
            `Daftar Mentor Berhasil, Silahkan Login Kembali.`
          );
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostJoin().failed(resErr.data.message);
        });
    },
    afterPostJoin() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelJoin() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin Join Mentor ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-join-mentor {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
}
</style>