<template>
  <div class="flash-sale" v-if="dataFS.length !== 0">
    <div class="container bg-linear-blue">
      <div class="row">
        <div class="col-sm-12 title-flash-sale">
          <div class="flash-sale-title">
            <h1>Flash Sale</h1>
            <img class="flash-circles-bg" src="/img/circle.png" alt />
          </div>
          <div class="flash-sale-time">
            <h1>{{ sisaWaktu }}</h1>
          </div>
        </div>
      </div>
      <div class="row justify-content-center">
        <div v-if="dataFS.length == 0" class="data-not-found">
          <h3>Belum ada Data Flash Sale</h3>
        </div>
        <template v-if="dataFS.length !== 0">
          <div class="col-sm-4 content-fs" v-for="(flashSale, idx) in dataFS" v-bind:key="idx">
            <div
              class="jadwal-kelas"
              v-bind:style="{
                'background': `url(${$root.getPhotoUser() + `/` + flashSale.banner} )`, 
                'background-repeat': 'no-repeat', 
                'background-size': 'cover'
              }"
            >
              <div class="jadwal-kelas-title">
                <h5>{{ flashSale.name }}</h5>
              </div>
              <div class="author">
                <div class="author-img">
                  <img src="/img/foto.jpg" alt />
                </div>
                <div class="author-nama">By {{ flashSale.mentor }}</div>
              </div>
              <div class="rating">
                <div class="bintang">
                  <star-rating :rating="flashSale.review.rating" :read-only="true" :star-size="20"></star-rating>
                </div>
                <div
                  class="angka"
                >{{ flashSale.review.rating }} ( {{ flashSale.review.count }} Rating)</div>
              </div>
              <div class="price">
                <div class="price-del">Rp.{{ flashSale.hargaCoret }}</div>
                <div class="price-now">Rp.{{ flashSale.hargaFlash }}</div>
              </div>
              <button class="pilih-jadwal btn">
                <router-link v-bind:to="flashSale.url">Pilih Jadwal</router-link>
              </button>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import StarRating from "vue-star-rating";
import * as moment from "moment";

export default {
  name: "FlashSaleMenu",
  components: {
    StarRating
  },
  data() {
    return {
      sisaWaktu: "12 : 05 : 05",
      flashSaleSchedule: [
        {
          id: 1,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          hargaFlash: "100.000",
          hargaCoret: "99.000"
        },
        {
          id: 2,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          hargaFlash: "100.000",
          hargaCoret: "99.000"
        },
        {
          id: 3,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          hargaFlash: "100.000",
          hargaCoret: "99.000"
        }
      ],
      dataFS: [
        // {
        //   uuid: "2304fd2e-0d73-4ce9-b981-5427e75db894",
        //   url: "/belajar-content-writing",
        //   banner: "65f47939a838262fe70c1e2624e1644a.png",
        //   name: "Belajar Content Writing",
        //   hargaCoret: 100000,
        //   hargaJual: 90000,
        //   hargaFlash: 79000,
        //   startAt: "2020-05-01 00:00:00",
        //   expiredAt: "2020-06-30 23:59:59",
        //   mentor: "Mentor",
        //   review: {
        //     count: 0,
        //     rating: 0
        //   }
        // }
      ]
    };
  },
  methods: {
    init() {
      // banner: "20200512_111701-600x600.jpg"
      // expiredAt: "2020-07-02 07:59:59"
      // hargaCoret: 249000
      // hargaFlash: 790000
      // hargaJual: 500000
      // mentor: "Nasrullah"
      // name: "Copywriting Dahsyat"
      // review: Object
      //   count: 0
      //   rating: 0
      // slug: "copywriting-dahsyat"
      // startAt: "2020-06-01 08:00:00"
      // uuid: "3f61dde0-f166-43d5-a4d0-967e5841bbfe"
      this.getListFlashSale();
    },
    getListFlashSale() {
      axios.get(this.$root.listFlashSalePage())
        .then((res) => {
          let data = res.data;
          let result = data.result;
          console.log("res ", res, result);

          this.dataFS = result.map((e) => {
            let url = `/detail-kelas/${e.slug}`;
            let obj = {
              banner: e.banner,
              expiredAt: e.expiredAt,
              hargaCoret: e.hargaCoret,
              hargaFlash: e.hargaFlash,
              hargaJual: e.hargaJual,
              mentor: e.mentor,
              name: e.name,
              review: e.review,
              slug: e.slug,
              url: url,
              startAt: e.startAt,
              uuid: e.uuid,
            };
            return obj
          });

          if ( this.dataFS.length > 0 ) {
            this.setTimeLeft();
          }
          setTimeout(() => {
            $(".vue-star-rating-rating-text").hide();
          }, 1500);
        })
        .catch((err) => {
          console.log("err ", err);
        });
    },
    setTimeLeft() {
      let time = this.dataFS[0].expiredAt;
      // The data/time we want to countdown to
      // var countDownDate = new Date("Jul 25, 2021 16:37:52").getTime();
      var countDownDate = moment(time).valueOf();

      // Run myfunc every second
      var myfunc = setInterval(() => {
        var now = new Date().getTime();
        var timeleft = countDownDate - now;

        // Calculating the days, hours, minutes and seconds left
        var days = Math.floor(timeleft / (1000 * 60 * 60 * 24));
        var hours = Math.floor(
          (timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        var minutes = Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((timeleft % (1000 * 60)) / 1000);

        // Result is output to the specific element
        let showTime = `${days} hari ${hours}:${minutes}:${seconds}`;
        this.sisaWaktu = showTime;

        // document.getElementById("days").innerHTML = days + "d ";
        // document.getElementById("hours").innerHTML = hours + "h ";
        // document.getElementById("mins").innerHTML = minutes + "m ";
        // document.getElementById("secs").innerHTML = seconds + "s ";

        // Display the message when countdown is over
        if (timeleft < 0) {
          clearInterval(myfunc);
          // document.getElementById("days").innerHTML = ""
          // document.getElementById("hours").innerHTML = ""
          // document.getElementById("mins").innerHTML = ""
          // document.getElementById("secs").innerHTML = ""
          // document.getElementById("end").innerHTML = "TIME UP!!";
        }
      }, 1000);
    },
  },
  mounted() {
    this.init();
  }
};
</script>

