<template>
  <div>
    <HeaderTop />
    <section class="section-reset-password">
      <div class="content-reset-password container">
        <div class="inp-form-fp">
          <h5>Reset Password</h5>
          <div class="input-form">
            <label for="password-login">New Password</label>
            <input
              type="password"
              name="password-login"
              class="form-input"
              placeholder="New Password"
              v-model="password.new"
            />
          </div>
          <div class="input-form">
            <label for="password-login">Re-Type New Password</label>
            <input
              type="password"
              name="password-login"
              class="form-input"
              placeholder="Re-Type New Password"
              v-model="password.retype"
              @keyup="checkMatchPasswd()"
              @keyup.enter="postResetPasswd()"
            />
            <span class="paswd-match" v-if="password.match && password.retype !== ''">
              <i class="fa fa-check"></i> Password is Match
            </span>
            <span class="paswd-not-match" v-if="!password.match">
              <i class="fa fa-times"></i> Password Not Match
            </span>
          </div>
          <button class="btn btn-login" v-on:click="postResetPasswd()">Reset Password</button>
        </div>
      </div>
    </section>
    <Footer />
  </div>
</template>

<script>
import axios from "axios";

import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "ResetPassword",
  components: {
    HeaderTop,
    Footer
  },
  data() {
    return {
      password: {
        new: "",
        retype: "",
        match: true
			},
			token: this.$route.params.token
    };
  },
  methods: {
    init() {},
    checkMatchPasswd() {
      let a = this.password;
			// console.log('pass ', a.new, a.retype, a.match);

      if (a.new.length == a.retype.length && a.new == a.retype) {
				a.match = true;
				return true;
      } else {
				a.match = false;
        return false;
			}
			
    },
    postResetPasswd() {
      if (!this.checkMatchPasswd()) {
        this.$root.notification("warning", "Warning", "Password Not Match.");
      } else {
        this.$root.loadingData().show();

        let body = {
          password: this.password.retype
        };

        axios
          .post(this.$root.resetPasswordAuth() + `/${this.token}`, body)
          .then(res => {
            console.log("response ", res);

            this.afterPostRPmsg().success("Reset Password Berhasil, Silahkan Login Kembali.");
            this.$root.loadingData().hide();
          })
          .catch(err => {
            let resErr = err.response;
            console.log("error ", resErr);

						if ( resErr.data.errors.name == "TokenExpiredError" ) {
							this.afterPostRPmsg().failed("Masa waktu reset password sudah Habis.");
						}

            this.$root.loadingData().hide();
          });
      }
    },
    afterPostRPmsg() {
      return {
        success: (msg) => {
          this.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                handler: () => {
                  this.$router.push("/signin/user/login");
                  this.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed: (msg) => {
          this.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                handler: () => {
                  this.$router.push("/");
                  this.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.section-reset-password {
  height: 500px;
  font-family: "Montserrat";
  .content-reset-password {
    text-align: left;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    > .inp-form-fp {
      flex-direction: column;
      width: 400px;
    }
    .btn-login {
      cursor: pointer;
      background: #004c94;
      color: #ffffff;
      font-weight: bold;
      margin: 10px 0;
    }
    .input-form {
      display: flex;
      flex-direction: column;
      > label {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 24px;
        margin: 5px 0 0 0;
      }
      > input {
        padding: 5px 5px 5px 10px;
        margin: 5px 0 0 0;
        border-radius: 8px;
        border: 1px solid #cccccc;
        &[type="password"] {
          padding: 5px 35px 5px 10px;
        }
      }
      > span {
        text-align: right;
        &.paswd-not-match {
          color: #f50c0c;
        }
        &.paswd-match {
          color: #09e41c;
        }
      }
    }
  }
}
</style>