<template>
  <div class="content-list-laporan">
    <h3 class="title-lk">List Laporan</h3>
    <div class="top-table-mbx"></div>
    <div class="filter-table-mbx">
      <h5>Filter :</h5>
      <div class="form-filter">
        <ul class="list-f-filter d-flex">
          <li>
            <div class="left">
              <span>Year :</span>
            </div>
            <div class="right d-flex">
              <div class="schedule-kelas">
                <!-- <h5 class="t-schedule">Jadwal Kelas</h5> -->
                <div class>
                  <div id="filterYear" class="date-kelas" v-on:click="openFilterYear()">
                    <div class="choose-date">
                      <img src="/icon/calendar.svg" />
                      <p class="text-date">{{ dataBody.year }}</p>
                    </div>
                    <i class="lnr lnr-chevron-down" id="activeDateYear"></i>
                  </div>
                  <ul id="listDateYear" class="list-date-kelas">
                    <li
                      class="date-kl"
                      v-for="(list, idx) in dataYear"
                      v-bind:key="idx"
                      v-on:click="chooseYearList(list, list.value == chooseYear)"
                    >
                      <input
                        class="radio-kl radio-mbx"
                        type="radio"
                        name="year-list"
                        v-bind:id="'radio-kl-' + idx"
                        v-bind:checked="list.value == dataBody.year"
                      />
                      <label v-bind:for="'radio-kl-' + idx" class="text-date">{{ list.text }}</label>
                      <!-- <p class="status-kl-available" v-if="list.available">Available</p>
                      <p class="status-kl-unavailable" v-else>Unvailable</p>-->
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <!-- <th>
                <div class="th-tbl-mbx">Action</div>
              </th>-->
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">Produk</div>
              </th>
              <th>
                <div class="th-tbl-mbx">Mentor</div>
              </th>
              <th>
                <div class="th-tbl-mbx">Total Member</div>
              </th>
              <th>
                <div class="th-tbl-mbx">Total Omset</div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr 
							v-for="(list, index) in dataTable.list" 
							v-bind:key="index"
              v-bind:hidden="!(index >= dataTable.minIndex && index <= dataTable.maxIndex)"
						>
              <!-- <td style="max-width: 100px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailLaporan(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button @click="editDataLaporan(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td>-->
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.produk }}</td>
              <td>{{ list.mentor }}</td>
              <td>{{ list.member }}</td>
              <td style="font-weight: 600;" >Rp.{{ list.omset }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="data-list-kosong" v-if="dataTable.list.length == 0">Data List Laporan Kosong.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import * as moment from "moment";

export default {
  name: "ListLaporan",
  components: {},
  data() {
    return {
      dataBody: {
        year: moment().format("YYYY"),
        month: moment().format("MM")
      },
      dataYear: [],
      dataTable: {
        list: [],
        colSearch: {
        },
        colSort: {
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        totalData: 0,
        maxIndex: 0,
        minIndex: 0,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
          ]
        }
      }
    };
  },
  methods: {
    init() {

      this.getListTahunLaporan();
    },
    getListTahunLaporan() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listTahunLaporanOwner() , {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res list tahun ", res);
					let data = res.data;
					let result = data.data;
					this.dataYear = [];
					this.dataYear = result.map(e => {
						let obj = {
              text: e.year,
              value: e.year
						};
						return obj;
					});

          this.getListLaporan();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    getListLaporan() {
      let a = this.dataBody;
      let sQuery = `?year=${a.year}&month=${a.month}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listLaporanOwner() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
					let data = res.data;
					let result = data.data;
					this.dataTable.list = [];
					this.dataTable.list = result.map(e => {
						let obj = {
							member: e.member.count,
							mentor: e.mentor,
							omset: e.omset,
							produk: e.produk,
						};
						return obj;
					});
					
					this.dataTable.totalData = data.count;
					this.tableMbx().counterPageTable(data.count);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search: (field) => {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
            this.dataTable.colSearchValue[field] = "";
            let result = this.tableMbx().filterCache();
            this.dataTable.list = result;
            this.tableMbx().counterPageTable(result.length);
          }
        },
        filterSearch: () => {
          let result = this.tableMbx().filterCache();
          console.log('result ', result);
          this.dataTable.list = result;
          this.tableMbx().counterPageTable(result.length);
        },
        filterCache: () => {
          let result = this.dataTable.listTemp;
          let col = [
            'kode',
            'bank',
            'pemilik',
            'rekening',
            'status',
          ];
          for (let j = 0; j < col.length; j++) {
            if (this.dataTable.colSearchValue[col[j]] != '') {
              result = result.filter(search => {
                var name = String(search[col[j]]).toUpperCase();
                var findme = new RegExp(
                  String(this.dataTable.colSearchValue[col[j]]).toUpperCase(),
                  'g',
                );
                return name.match(findme);
              });
            }
          }
          return result;
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          vm.tableMbx().counterPageTable(vm.dataTable.list.length);
          vm.tableMbx().perPage();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        },
        counterPageTable: (length) => {
          let m = Math.ceil(length / a.perPage);
          if (m <= 1) {
            m = 1;
          }
          let lengthData = Array(m)
              .fill(0, 0)
              .map((x, i) => i).length
            a.page.total = lengthData;
            console.log('lengthData ', lengthData, m);
          vm.tableMbx().changeShowPage();
        },
        changeShowPage: () => {
          let pageNow = vm.dataTable.page.now;
          let perPage = vm.dataTable.perPage;
          this.dataTable.minIndex = ((pageNow*perPage)-perPage);
          this.dataTable.maxIndex = ((pageNow*perPage)-1);
        }
      };
    },
    openFilterYear() {
      $("#filterYear").toggleClass("active");
      $("#activeDateYear").toggleClass("lnr-chevron-down lnr-chevron-up");
      $("#listDateYear").slideToggle();
    },
    chooseYearList(data, cond) {
      // this.openFilterYear();
      let a = this.dataBody;
      if (!cond) {
				a.year = data.value;
        this.getListLaporan();
      }
    },
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-laporan {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
.schedule-kelas {
  display: flex;
  > .t-schedule {
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 34px;
    color: #000000;
    margin: 0%;
  }
  .date-kelas {
    display: flex;
    margin: 0 20px;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 7px;
    cursor: pointer;
    width: 300px;
    &.active {
      border-radius: 7px 7px 0 0;
    }
    > .choose-date {
      display: flex;
      align-items: center;
      > p {
        margin: 3px 20px;
      }
    }
    > i {
      margin: auto 0 auto auto;
      padding: 0 0 0 10px;
    }
  }
  .list-date-kelas {
    padding: 5px 10px;
    background: #ccc;
    color: #ffffff;
    margin: 0 20px;
    border-radius: 0 0 7px 7px;
    display: none;
    position: absolute;
    width: 300px;
    > .date-kl {
      list-style: none;
      display: flex;
      align-items: center;
      padding: 3px 0;
      &[readonly="readonly"] {
        opacity: 0.5;
        cursor: not-allowed;
        > label {
          cursor: not-allowed;
        }
      }
      > .text-date {
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 15px;
        width: 100%;
        color: #ffffff;
        margin: 7px 0;
        padding: 0px 0 0 39px;
      }
      > p {
        color: #ffffff;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        width: 95px;
        padding: 3px;
        text-align: center;
        margin: 0px 0 0 auto;
        border-radius: 20px;
        &.status-kl-available {
          background: #219653;
        }
        &.status-kl-unavailable {
          background: #e40000;
        }
      }
    }
  }
}
</style>