<template>
    <section 
        class="maintenance-page"
        v-bind:style="{ height: window.height+'px' }"
    >
        <div class="content-mp">
            <div class="message">
                <img class="mbx-logo" src="../assets/img/mentorboxlogo-parent.png" alt />
                <h2>In Maintenance</h2>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: 'MaintenancePage',
    components: {},
    data() {
        return {
            window: {
                width: 0,
                height: 0
            },
        }
    },
    created() {
        window.addEventListener("resize", this.handleResize);
        this.handleResize();
    },
    destroyed() {
        window.removeEventListener("resize", this.handleResize);
    },
    methods: {
        init() {
            this.handleResize();
        },
        handleResize() {
            this.window.width = window.innerWidth;
            this.window.height = window.innerHeight;
        },
    },
    mounted() {
        this.init();
    }
}
</script>

<style lang="scss" scoped>
.maintenance-page {
    font-family: "Montserrat";
    min-height: 550px;
    overflow: hidden;
    background-color: #004c94;
    > .content-mp {
        height: 100%;
        > .message {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #ffffff;
            flex-direction: column;
            height: 100%;
            > .mbx-logo {
                width: 250px;
            }
        }
    }
}
</style>