      <p>Dengan mengakses dan menggunakan website mentorbox.id serta mendaftar menjadi mentor di MentorBox.id, berarti Anda telah memahami dan menyetujui untuk terikat dan tunduk dengan semua peraturan yang berlaku di MentorBox.id.</p>
