        <li>Nomor telepon yang dicantumkan adalah nomor yang aktif, sehingga MentorBox.id dapat menghubungi sewaktu-waktu apabila diperlukan.</li>
        <li>MentorBox.id berhak untuk menolak aplikasi pendaftaran Anda sebagai Mentor dengan alasan apapun.</li>
        <li>Mentor dapat berasal dari wilayah mana saja di Indonesia</li>
