      <p>
        <strong>Akun Mentor</strong>
      </p>
      <ol>
        <li>Anda bertanggung jawab untuk menjaga kerahasiaan akun dan password dan untuk membatasi akses ke komputer Anda.</li>
        <li>Anda setuju bertanggung jawab atas semua yang terjadi di bawah akun Anda dengan password Anda.</li>
        <li>Anda setuju untuk segera memberitahukan MentorBox.id tentang setiap penyalahgunaan akun atau pelanggaran keamanan lainnya yang berkaitan dengan akun Anda.</li>
        <li>MentorBox.id tidak akan bertanggung jawab atas kerugian atau kerusakan yang timbul dari kegagalan untuk mematuhi persyaratan-persyaratan ini.</li>
        <li>MentorBox.id berhak untuk menolak layanan atau menghentikan akun.</li>
      </ol>
      <p>
        <strong>Profil Mentor</strong>
      </p>
