        <li>Anda, dan bukan MentorBox.id, bertanggung jawab penuh atas semua materi yang Anda sediakan melalui MentorBox.id.</li>
      </ol>
      <p>
        <strong>Kualifikasi dan Sertifikat</strong>
      </p>
