        <li>MentorBox.id tidak menjamin bahwa Anda akan menerima penugasan mengajar setelah Anda menerima dan menanggapi broadcast Pesan WhatsApp, email dan telepon.</li>
      </ol>
      <p>
        <strong>Pelajaran pertama dan Penugasan</strong>
      </p>
      <ol>
        <li>Anda setuju untuk menghubungi pelanggan sebelum pelajaran pertama untuk konfirmasi informasi kegiatan belajar mengajar.</li>
