<template>
  <div class="form-register">
    <h1>Register Ke Akun Google Anda</h1>
    <div id="contentFRGoogle" class="content-form-register-google container">

    </div>
  </div>
</template>

<script>
// import $ from "jquery";
import axios from "axios";

export default {
  name: "RegisterGoogle",
  components: {},
  data() {
    return {
      urlGoogle: this.$root.getGoogle(),
      formRegister: {
        email: "",
        password: ""
      },
      cannotNull: {
        email: true,
        password: true
      },
      formatEmail: true
    };
  },
  methods: {
    init() {
      // this.getFormGoogle();

      window.open( this.urlGoogle );
    },
    getFormGoogle() {
      let url = this.urlGoogle;
      axios.get( url )
      .then((res) => {
        console.log('res ', res);
      })
      .catch((err) => {
        console.log('err ', err);
      })
    },
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.form-register {
  font-family: "Montserrat";
  text-align: left;
  display: flex;
  flex-direction: column;
  > h1 {
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
  }
  .content-form-register-google {
    height: auto;
  }
  > .input-form {
    display: flex;
    flex-direction: column;
    > label {
      font-style: normal;
      font-weight: normal;
