    }
    > input {
      padding: 5px 5px 5px 10px;
      margin: 5px 0 0 0;
      border-radius: 8px;
      border: 1px solid #cccccc;
      &[type="password"] {
        padding: 5px 35px 5px 10px;
      }
    }
    > .sh-password {
