<template>
  <div class="content-jadi-mentor">
    <div v-if="!mentorRole">
      <div class="content-jm">
        <h1 class="title-jm">Bergabung Menjadi Mentor?</h1>
        <p class="p-jm">Karena untuk mencapai kesuksesan diperlukan kemahiran yang dipelajari oleh siapa saja tanpa batas ruang dan waktu, Bantu kami memenuhi kebutuhan skills dengan bergabung menjagi Mentor dimana Anda bisa fleksibel mengatur waktu penyampaian materi dan diskusi. Memanfaatkan WhatsApp Group menjadi kanal belajar online yang efektif saat ini.</p>
      </div>
      <div class="input-form">
        <p style="margin: 10px 0 0 0;">
          Baca
          <router-link v-bind:to="'/kebijakan-mentorbox/sk-menjadi-mentor'" >Syarat dan Ketentuan Mentor.</router-link>
        </p>
        <input
          type="checkbox"
          name="check-voucher"
          class="checkbox-mbx"
          v-bind:checked="agreement"
        />
        <label 
          for="checkV" 
          class="checklist"
          @click="agreement ? agreement = false : agreement = true;"
        >Saya sudah membaca dan mengerti dengan syarat dan ketentuan menjadi Mentor.</label>
      </div>
      <button 
        class="btn btn-jadi-mentor"
        @click="joinMentor()"
        v-bind:disabled="!agreement"
      >Join Mentor</button>
    </div>
    <div v-else>
      <div class="d-flex justify-content-center flex-column">
        <h5>Anda Seorang Mentor</h5>
        <button 
          class="btn btn-jadi-mentor"
          @click="goToDashboardMentor()"
        >Go To Dashboard Mentor</button>
      </div>
    </div>

    <modal name="executor-join-mentor" :width="'80%'" :height="'70%'" :clickToClose="false">
      <ExecutorJoinMentor />
    </modal>
  </div>
</template>

<script>
import ExecutorJoinMentor from './ExecutorJoinMentor';

export default {
  name: "JadiMentor",
  components: {
    ExecutorJoinMentor,
  },
  data() {
    return {
      mentorRole: this.$root.checkRole('MENTOR'),
      agreement: true
    }
  },
  methods: {
    init() {
      console.log('check role mentor ', this.mentorRole);
    },
    goToDashboardMentor() {
      let loginData = window.$cookies.get('loginData');
      let uuid = loginData.result.uuid;
      let url = `/dashboard-mentor/${uuid}`;
      this.$router.push(url);
    },
    joinMentor() {
      this.$modal.show('executor-join-mentor');
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-jadi-mentor {
	padding: 0 0 30px 0;
	font-family: "Montserrat";
  > .title-jm {
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 44px;
    color: #000000;
  }
  .content-jm {
    background: #cbcdce;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
    border-radius: 8px;
    padding: 25px;
    text-align: left;
    > .p-jm {
      color: #000000;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 20px;
    }
  }
  .btn-jadi-mentor {
    margin: 15px auto 0 0;
    display: flex;
    font-weight: 600;
    background: #003461;
    color: #ffffff;
    border: 1px solid #003461;
    padding: 5px 35px;
    &:hover {
      border: 1px solid #003461;
      background: #ffffff;
      color: #003461;
    }
  }
}
.input-form {
  text-align: left;
  margin: 20px 0 0;
  > label {
    font-size: 20px;
    text-align: left;
    justify-content: flex-start !important;
    &.checklist {
      font-size: 16px;
      margin: 0;
      &::before {
        padding: 0 4px 4px 4px;
        margin: 0 10px 0 0;
      }
    }
  }
}
@media (min-width: 768px) {
  .content-jadi-mentor {
    .content-jm {
      > .title-jm {
        font-size: 33px;
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .content-jadi-mentor {
    .content-jm {
      > .title-jm {
        font-size: 30px;
      }
    }
  }
}
@media (max-width: 481px) {
  .content-jadi-mentor {
    .content-jm {
      > .title-jm {
        font-size: 25px;
      }
    }
  }
}
</style>