, alamat surat elektronik, dan nomor telepon genggam Anda yang Anda berikan ketika Anda membuka Akun tunduk pada Kebijakan Privasi, yang merupakan bagian yang tidak terpisahkan dari Syarat dan Ketentuan ini. ")]),_c('p',[_c('br'),_c('strong',[_vm._v("C. AKSES TERHADAP PLATFORM KAMI")])]),_c('p',[_vm._v(" Kami tidak menjamin bahwa Platform Kami, maupun konten di dalamnya, akan selalu tersedia atau tanpa gangguan. Izin untuk mengakses Platform Kami bersifat sementara. Kami dapat menangguhkan, menarik, memberhentikan, maupun mengganti bagian mana pun dari Platform Kami tanpa pemberitahuan sebelumnya. Kami tidak bertanggung jawab atas alasan apa pun yang membuat Platform Kami tidak tersedia pada waktu atau periode tertentu. ")]),_c('p',[_vm._v(" Anda bertanggung jawab untuk membuat semua pengaturan yang dibutuhkan untuk mendapatkan akses terhadap Platform Kami. ")]),_c('p',[_c('br'),_c('strong',[_vm._v("D. PENGGUNAAN YANG DILARANG")])]),_c('p',[_vm._v(" Anda hanya diperbolehkan menggunakan Platform kami untuk tujuan-tujuan yang sah menurut hukum. Anda tidak boleh menggunakan Platform Kami: ")]),_c('ol',[_c('li',[_vm._v("Dengan cara-cara yang melanggar hukum dan peraturan lokal, nasional, maupun internasional yang berlaku.")]),_c('li',[_vm._v("Dengan cara-cara yang melanggar hukum atau menipu, atau memiliki tujuan atau dampak yang melanggar hukum atau menipu.")]),_c('li',[_vm._v("Mengirim, secara sadar menerima, mengunggah, mengunduh, menggunakan, atau menggunakan kembali materi yang tidak sesuai dengan standar konten Kami.")]),_c('li',[_vm._v("Menyebarkan atau mengirimkan materi iklan atau promosi yang tidak diinginkan atau tidak sah, serta bentuk permintaan serupa lainnya (seperti spam).")]),_c('li',[_vm._v("Dengan sengaja meneruskan data, mengirim atau mengunggah materi yang mengandung virus, trojan, worm, logic bomb, keystroke loggers, spyware, adware, maupun program berbahaya lainnya atau kode komputer sejenis yang dirancang untuk memberikan efek merugikan terhadap 