<template>
  <section class="blog-terkait-section" v-if="listBlogTerkait.length !== 0">
    <div class="container">
      <div class="content-blog-terkait">
        <div class="title-blog-terkait">
          <h1>Blog Terkait</h1>
        </div>
        <div class="row">
          <div class="col-sm-4 ta-choose" v-for="data in listBlogTerkait" v-bind:key="data.id">
            <div class="content-bt">
              <div class="img-article">
                <img v-bind:src="data.img_article" v-bind:alt="data.title" />
              </div>
              <div class="message-bt">
                <div class="title-author">
                  <img class="img-author" v-bind:src="data.img_author" v-bind:alt="data.author" />
                  <p class="by-author">
                    <span>Oleh</span>
                    {{data.author}}
                  </p>
                </div>
                <h1 class="title-bt">{{ data.title }}</h1>
                <div class="row date-comment-bt">
                  <div class="col-sm-6 left-date-comment">
                    <img src="/icon/calendar.svg" />
                    <p class="date-bt">{{ data.date_article }}</p>
                  </div>
                  <div class="col-sm-6 right-date-comment">
                    <img src="/icon/comment.svg" />
                    <p class="comment-bt">{{ data.amount_comment }} Comment</p>
                  </div>
                </div>
                <div class="description-bt">
                  <p>{{ data.description }}...</p>
                </div>
                <router-link v-bind:to="data.url" class="btn-bt">
                  <button class="btn">Selengkapnya</button>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import * as moment from "moment";

export default {
  name: "BlogTerkait",
  components: {},
  data() {
    return {
      slugBlog: this.$route.params.slug,
      tipsArticleData: [
        {
          id: 1,
          img_article: "/img/tips-article-1.png",
          author: "Andri Darmawan",
          img_author: "/img/foto.jpg",
          title: "Heboh Tiktok kini menghiasi Kurikulum 2020",
          date_article: "25 April 2020",
          amount_comment: "0",
          description:
            "dipelajari oleh siapa saja tanpa batas ruang dan waktu, bantu kami memenuhi kebutuhan skills dengan bergabung menjadi",
          url: "/blog/heboh-tiktok-kini-menghiasi-kurikulum-2020"
        },
        {
          id: 2,
          img_article: "/img/tips-article-2.png",
          author: "Andri Darmawan",
          img_author: "/img/foto.jpg",
          title: "Work From Home Ini dia 5 Fakta Serunya",
          date_article: "24 April 2020",
          amount_comment: "1",
          description:
            "dipelajari oleh siapa saja tanpa batas ruang dan waktu, bantu kami memenuhi kebutuhan skills dengan bergabung menjadi",
          url: "/blog/serunya-work-from-home"
        },
        {
          id: 3,
          img_article: "/img/tips-article-3.png",
          author: "Andri Darmawan",
          img_author: "/img/foto.jpg",
          title: "Siapa bilang koran lama ditinggalkan?",
          date_article: "23 April 2020",
          amount_comment: "2",
          description:
            "dipelajari oleh siapa saja tanpa batas ruang dan waktu, bantu kami memenuhi kebutuhan skills dengan bergabung menjadi",
          url: "/blog/siapa-bilang-koran-lama-di-tinggalkan"
        }
      ],
      listBlogTerkait: []
    };
  },
  methods: {
    init() {
      // Tags: (6) [{…}, {…}, {…}, {…}, {…}, {…}]
      // author: {name: "Change Profile", photo: "avatar.png"}
      // banner: "afae78fb38b50134ac443e37df7ddec2.jpg"
      // createdAt: "2020-06-06 15:09:50"
      // description: "Mari kita maju bersama. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolo"
      // slug: "bebas-rasisme"
      // title: "Bebas Rasisme"
      // uuid: "87cd6dea-b9a2-4bb2-b14c-b1b9e12bd314"

      this.getListBlog();
    },
    getListBlog() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.blogTerkaitPage() + `/${this.slugBlog}`)
        .then(res => {
          let data = res.data;
          let result = data.result;
          this.listBlogTerkait = result.map(e => {
            let obj = {
              img_article: e.banner,
              author: e.author.name,
              img_author: e.author.photo,
              title: e.title,
              date_article: moment(e.createdAt).format("DD - MMM -YYYY"),
              amount_comment: "0",
              description: `${e.description.slice(0, 200)}`,
              url: `/blog/${e.slug}`
            };
            return obj;
          });

          console.log("res ", result);
          this.$root.loadingData().hide();
        })
        .catch(err => {
          console.log("err ", err);
          this.$root.loadingData().hide();
        });
    }
  },
  mounted() {
    this.init();
  },
  watch: {
    $route(to, from) {
      to; from;
      this.init();
    }
  }
};
</script>

