<template>
  <section class="visi-misi-us">
    <div class="container">
      <div class="parent-tab">
        <ul class="tabs">
          <li
            v-for="tab in dataVisiMisi"
            v-bind:key="tab.id"
            v-bind:class="{active: tab.show, [ tab.sub_id ] : tab.show }"
            v-on:click="changeTab($event, tab.sub_id)"
          >
            <a>{{ tab.title_tab }}</a>
          </li>
          <hr />
        </ul>
      </div>
      <div class="content-visi-misi" v-if="dataVisiMisi[0].show">
        <p class="message text-center"> Mentoring Online Nomor 1 di Indonesia </p>
      </div>
      <div class="content-visi-misi" v-if="dataVisiMisi[1].show">
        <ol>
          <li
           v-for="(misi, idx) in dataVisiMisi[1].message"
           v-bind:key="idx"
          >{{ misi }}</li>
        </ol>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "VisiMisi",
  components: {},
  data() {
    return {
      dataVisiMisi: [
        {
          id: 1,
          sub_id: "visi",
          title_tab: "Visi",
          show: true,
          message:
            "Mentoring Online Nomor 1 di Indonesia"
        },
        {
          id: 2,
          sub_id: "misi",
          title_tab: "Misi",
          show: false,
          message: [
            'Memperluas akses orang-orang untuk menemukan mentor terbaik dibidangnya.',
            'Menyediakan layanan mentoring dengan mentor dan materi terbaik untuk bisa diaplikasikan langsung.',
            'Menyediakan teknologi untuk memudahkan semua klien mendapatkan mentoring berkualitas.'
          ]
        }
      ]
    };
  },
  methods: {
    changeTab(evt, id) {
      console.log("change Tab ", id);

      let dataTab = this.dataVisiMisi;
      for (let i = 0; i < dataTab.length; i++) {
        if (dataTab[i].sub_id == id) {
          dataTab[i].show = true;
        } else {
          dataTab[i].show = false;
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.visi-misi-us {
  margin: 10px 0 80px 0;
  .parent-tab {
    display: flex;
    ul.tabs {
      padding: 0%;
      width: 40%;
      margin: 10px auto;
      li {
        display: inline;
        text-align: center;
        cursor: pointer;
        font-style: normal;
        font-weight: normal;
        font-size: 48px;
        line-height: 59px;
        color: #003461;
        opacity: 0.56;

        a {
          display: inline-block;
          width: 50%;
          padding: 0.75rem 0;
          margin: 0;
          text-decoration: none;
        }
        &.misi ~ hr {
          margin-left: 50%;
        }
        &.active {
          opacity: 1;
        }
      }
      hr {
        height: 0.25rem;
        width: 50%;
        margin: 0;
        background: #c4c4c4;
        border: none;
        transition: 0.5s ease-in-out;
      }
    }
  }
  .content-visi-misi {
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    text-align: left;
    margin: 15px 0 0 0;
  }
}
