<template>
  <div class="main-dashboard-owner">
    <h3 class="title-lk">Main Dashboard Owner</h3>
		<div class="content-main-do">
			<div class="chart-data-line">
        <h5>Total Komisi Dan Omset</h5>
        <canvas id="chartDataLine" width="500" height="300"></canvas>
      </div>
		</div>
  </div>
</template>

<script>
import axios from 'axios';
import * as moment from 'moment';
import Chart from "chart.js";

export default {
  name: "MainDashboardOwner",
  components: {},
  data() {
    return {};
  },
  methods: {
		init() {
			this.getDataDashboard();
		},
		getDataDashboard() {
			this.$root.loadingData().show();
			let AuthSth = this.$root.authorization();

			axios.get(this.$root.dashboardOwner() , {
				headers: { Authorization: AuthSth }
			})
			.then((res) => {
				console.log('res ', res);
				let data = res.data;
				let komisi = data.komisi;
				let omset = data.omset;

				this.handleChartLine(komisi, omset);

				this.$root.loadingData().hide();
			})
			.catch((err) => {
				console.log('err ', err);

				this.$root.loadingData().hide();
			})
		},
		handleChartLine(komisi, omset) {
			let year = moment().format('YYYY');
			let labels = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Des"
      ];
			var canvas = document.getElementById("chartDataLine");
      new Chart(canvas, {
        type: "line",
        data: {
          labels: labels,
          datasets: [
            {
              data: komisi.map((e) => {
								return e.data;
							}),
              label: "Komisi",
              borderColor: "#f2994a",
              fill: false
            },
            {
              data: omset.map((e) => {
								return e.data;
							}),
              label: "Omset",
              borderColor: "#004c94",
              fill: false
            }
          ]
        },
        options: {
          title: {
            display: true,
            text: `Transaction in Year ${ year }`
          }
        }
      });
		}
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.main-dashboard-owner {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
}
</style>